import {Component, ViewChild} from '@angular/core';
import { IonicPage, NavController, NavParams, Content } from 'ionic-angular';
import { RegistrazionePage } from "../registrazione/registrazione";

/**
 * Generated class for the PrivacyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-privacy',
  templateUrl: 'privacy.html',
})
export class PrivacyPage {

  @ViewChild(Content) contenuto: Content;
  pulsante_attivo:boolean=false;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  registrazione(){
    this.navCtrl.push(RegistrazionePage);
    //this.contenuto.scroll
  }

  indietro(){
    this.navCtrl.pop();
  }

  attiva(scrolling){
    setTimeout(() => {
      console.log("Attiva");
      this.pulsante_attivo = true;
      scrolling.complete();
    }, 500);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PrivacyPage');
  }

}



